
// , TODO: logger
function genModuleLogger(moduleName) {
  return {
    debug: console.log.bind(this, `[${moduleName}]`),
    info: console.log.bind(this, `[${moduleName}]`),
    warn: console.log.bind(this, `[${moduleName}]`),
    error: console.log.bind(this, `[${moduleName}]`)
  };
}

export default {
  genModuleLogger,
}